const messageId = localStorage.getItem('lastMessageId');

export class Message {
    static counter = messageId ? messageId : 1;

    constructor (sender, text) {
        this.id = Message.counter++;
        this.sender = sender;
        this.text = text;
        this.date = new Date();
    }

}

const folderId = localStorage.getItem('lastFolderId');

export class Folder {
    static counter = folderId ? folderId : 1;

    constructor (name, chains) {
        this.id = Folder.counter++;
        this.name = name;
        this.chains = chains;
    }

}

